<template>
	<div class='con' >
		<div v-if='!loaded' class='loading'>
			<img :src="loadingImg" class='img'>
			<span class='text light_color_2'>...</span>
		</div>
		<div v-else>
			<slot></slot>
		</div>
	</div>
</template>

<script type='es6'>
export default{
	props:{
		loaded:{
			type:Boolean,
			default:false
		},
		loadingImg:{
			type:String,
			default:'static/gears.svg'
		}
	},
	data(){
		return{
			
		}
	},
	computed:{
		
	},
	methods:{
		
	}
}
</script>

<style lang='scss' scoped>
$img_width:40px;
$font_size:4vw;
.con{
	height: 100%;
	width: 100%;
	overflow-y: auto;
	position: relative;
}
.withAnimate{
	transition: all 0.3s ease-in-out
}
.loading{
	position: absolute;
	top:0;
	bottom:0;
	width: calc(40px + 3vw + 4vw * 5);
	height:$img_width;
	right: 0;
	left:0;
	font-size: $font_size;
	margin: auto;
}

.img{
	width: $img_width;
	height: $img_width;
	vertical-align: middle;
}

.text{
	display: inline-block;
	margin-left: 3vw;
}
</style>


